// components/projects/ProjectDetails.js

import React, { Component } from "react";
import axios from "axios";
import { Link } from "react-router-dom";
import EditProject from "./EditProject";
import AddTask from "../tasks/AddTask";

class ProjectDetails extends Component {
  constructor(props) {
    super(props);
    this.state = {
      project: null,
    };
    this.getSingleProject = this.getSingleProject.bind(this);
    this.deleteProject = this.deleteProject.bind(this);
  }

  getSingleProject() {
    console.log(this.props);
    const { id } = this.props.match.params;

    axios
      .get(process.env.REACT_APP_API_URL + "/projects/" + id, {
        withCredentials: true,
      })
      .then((response) => this.setState({ project: response.data }))
      .catch((error) => console.log(error));
  }

  componentDidMount() {
    this.getSingleProject();
  }

  deleteProject() {
    axios
      .delete(
        process.env.REACT_APP_API_URL + "/projects/" + this.state.project._id,
        {
          withCredentials: true,
        }
      )
      .then(() => this.props.history.push("/projects"))
      .catch((error) => console.log(error));
  }

  ownershipCheck(project) {
    if (
      this.props.loggedInUser &&
      project.owner === this.props.loggedInUser._id
    ) {
      return (
        <div>
          <div>
            <EditProject
              theProject={this.state.project}
              getTheProject={this.getSingleProject}
              {...this.props}
            />
          </div>
          <button className="button" onClick={this.deleteProject}>
            Delete project
          </button>
        </div>
      );
    }
  }

  render() {
    const { project } = this.state;

    return (
      <div>
        {project ? (
          <>
            <h1 className="title is-1">{project.title}</h1>
            <figure className="image is-4by3">
              {project.imageUrl ? (
                <img src={project.imageUrl} alt={project.title} />
              ) : (
                <img
                  src="https://versions.bulma.io/0.7.0/images/placeholders/256x256.png"
                  alt={project.title}
                />
              )}
            </figure>
            <p>{project.description}</p>
            <h2>Tasks</h2>
            <ul>
              {project.tasks.map((task, idx) => (
                <li key={idx}>
                  <Link to={`/projects/${project._id}/tasks/${task._id}`}>
                    {task.title}
                  </Link>
                </li>
              ))}
            </ul>
            {this.ownershipCheck(project)}
            <br />
            <AddTask
              theProject={project}
              getTheProject={this.getSingleProject}
            />
            <br />
            <Link to={"/projects"}>Back to projects</Link>
          </>
        ) : (
          <h4>loading...</h4>
        )}
      </div>
    );
  }
}

export default ProjectDetails;
